<template>
  <div>
    <v-container>
      <v-row>
        <v-col cols="12">
          <!-- data table -->
          <crud-data-table
            :table="{
              search: true,
              showSelect: true,
              heading: $t('crud.table.headings.utilities.contacts'),
              button: {
                text: $t('crud.table.addNewButtons.utilities.contacts')
              }
            }"
            :add-edit-dialog="{
              addHeading: $t('addEditDialog.headings.add.utilities.contacts'),
              editHeading: $t('addEditDialog.headings.edit.utilities.contacts')
            }"
            :headers="headers"
            :default-item="item"
            :actions="{
              edit: true,
              delete: true
            }"
            :fetch-data="{
              url: '/api/contacts',
              data: 'contacts'
            }"
            :add-data="{
              url: '/api/contacts',
              reload: true
            }"
            :edit-data="{
              url: '/api/contacts/:param',
              param: '_id',
              reload: true
            }"
            :delete-data="{
              url: '/api/contacts/:param',
              param: '_id',
              reload: true
            }"
            :delete-multiple-data="{
              url: '/api/contacts/delete',
              arrayOf: '_id',
              reload: true
            }"
            @after-add="
              $store.dispatch('contact/fetchContacts', { axios: $axios })
            "
            @after-edit="
              $store.dispatch('contact/fetchContacts', { axios: $axios })
            "
          >
            <template v-slot:[`item.icon`]="{ item: contact }">
              <v-icon>{{ contact.icon }}</v-icon>
            </template>
            <template v-slot:crud-form="{ item: contact }">
              <contact-form :contact="contact" />
            </template>
          </crud-data-table>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
export default {
  components: {
    CrudDataTable: () => import('@/components/crud/CrudDataTable'),
    ContactForm: () => import('@/components/forms/utilities/ContactForm')
  },
  middleware: ['pagePermission'],
  data() {
    return {
      headers: [
        { text: this.$t('crud.table.headers.title'), value: 'title' },
        { text: this.$t('crud.table.headers.slug'), value: 'slug' },
        { text: 'Translation', value: 'traslation' },
        { text: this.$t('crud.table.headers.detail'), value: 'detail' },
        { text: this.$t('crud.table.headers.icon'), value: 'icon' },
        {
          text: this.$t('crud.table.headers.actions'),
          value: 'actions',
          sortable: false
        }
      ],
      item: { title: '', slug: '', translation: '', detail: '' }
    }
  }
}
</script>

